




































import songList from '@/components/songList.vue';
import nowPlay from '@/components/nowPlay.vue';
import mvList from '@/components/mvList.vue';
export default {
	data() {
		return {
			singerInfo: '',
			tabNum: 1,
			SingerDesc: '',
			singerHotSongs: '',
			singerMvs: ''
		};
	},
	components: {
		songList,
		nowPlay,
		mvList
	},
	onLoad(e) {
		var singerInfo = JSON.parse(e.data);
		this.singerInfo = singerInfo;
		this.singerInfo.picUrl = decodeURIComponent(this.singerInfo.picUrl);
		this.getSingerDesc(); //获取歌手描述
		this.getSingerSong(); //获取歌手单曲
		this.getSingerMv(); //获取歌手MV
	},
	methods: {
		getSingerDesc() {
			//获取歌手描述
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.artistDesc,
				data: {
					id: _this.singerInfo.id
				},
				success: res => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.SingerDesc = res.data;
					} else {
						console.error('数据获取失败');
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: err => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			});
		},
		getSingerSong() {
			//获取歌手单曲
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.artistSong,
				data: {
					id: _this.singerInfo.id
				},
				success: res => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.singerHotSongs = res.data.hotSongs;
					} else {
						console.error('数据获取失败');
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: err => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			});
		},
		getSingerMv() {
			//获取歌手mv
			var _this = this;
			uni.showLoading({
				title: '加载中'
			});
			uni.request({
				url: _this.MusicApi.server + _this.MusicApi.type.artistMv,
				data: {
					id: _this.singerInfo.id
				},
				success: res => {
					uni.hideLoading();
					//console.log(res.data);
					if (res.data.code == 200) {
						_this.singerMvs = res.data.mvs;
						//console.log(_this.singerMvs)
					} else {
						console.error('数据获取失败');
						uni.showToast({
							title: 'banner数据获取失败',
							duration: 5000,
							icon: 'none'
						});
					}
				},
				fail: err => {
					uni.hideLoading();
					uni.showToast({
						title: '请求超时',
						duration: 5000,
						icon: 'none'
					});
				}
			});
		}
	}
};
